"use client";
import { useState } from "react";

const mockPositionsInit = [
	{
		id: 1,
		asset: "ETH",
		size: 2,
		entryPrice: 2000,
		margin: 500,
		leverage: 5,
		isLong: true,
		isOpen: true,
	},
	{
		id: 2,
		asset: "BTC",
		size: 1,
		entryPrice: 30000,
		margin: 1000,
		leverage: 3,
		isLong: false,
		isOpen: false,
	},
];

export default function PositionsPage() {
	const [positions, setPositions] = useState<any[]>(mockPositionsInit);
	const [message, setMessage] = useState("");

	function handleClosePosition(id: number) {
		setPositions((p) =>
			p.map((pos) =>
				pos.id === id
					? { ...pos, isOpen: false, closePrice: pos.entryPrice }
					: pos
			)
		);
		setMessage("Closed position");
	}

	return (
		<div className="centered-container">
			<div className="max-w-xl w-full">
				<h1 className="text-3xl font-bold mb-4 shadow-title">Positions</h1>
				<p className="text-gray-600 mb-6">
					View and manage your open, closed, and liquidated positions.
				</p>
				<div className="card">
					<h2 className="text-xl font-semibold mb-2">Your Positions</h2>
					<table className="w-full text-left">
						<thead>
							<tr>
								<th className="py-1">ID</th>
								<th className="py-1">Asset</th>
								<th className="py-1">Size</th>
								<th className="py-1">Leverage</th>
								<th className="py-1">Type</th>
								<th className="py-1">Status</th>
								<th className="py-1">Action</th>
							</tr>
						</thead>
						<tbody>
							{positions.length === 0 && (
								<tr>
									<td
										colSpan={7}
										className="text-gray-400 py-2 text-center"
									>
										No positions
									</td>
								</tr>
							)}
							{positions.map((pos) => (
								<tr key={pos.id} className="border-t">
									<td className="py-1">{pos.id}</td>
									<td className="py-1">{pos.asset}</td>
									<td className="py-1">{pos.size}</td>
									<td className="py-1">{pos.leverage}x</td>
									<td className="py-1">
										{pos.isLong ? "Long" : "Short"}
									</td>
									<td className="py-1">
										{pos.isOpen ? "Open" : "Closed"}
									</td>
									<td className="py-1">
										{pos.isOpen && (
											<button onClick={() => handleClosePosition(pos.id)}>
												Close
											</button>
										)}
										{!pos.isOpen && (
											<span className="text-gray-400">Closed</span>
										)}
									</td>
								</tr>
							))}
						</tbody>
					</table>
					{message && (
						<div className="mt-4 text-[#6366f1] font-semibold">
							{message}
						</div>
					)}
				</div>
			</div>
		</div>
	);
}